import React from 'react';
import { Link } from 'react-router-dom';

class Header extends React.Component {
    render() {
        return (
            <>
                {/* <!-- Navbar --> */}
                <nav class="navbar navbar-expand-md navbar-dark navbar-custom fixed-top">
                    {/* <!-- Text Logo - Use this if you don't have a graphic logo --> */}
                    {/* <!-- <a class="navbar-brand logo-text page-scroll" href="index.html">Aria</a> --> */}

                    {/* <!-- Image Logo --> */}
                    <Link to="/" class="navbar-brand logo-image" ><img src="assets/images/logo.svg" alt="alternative" /></Link>

                    {/* <!-- Mobile Menu Toggle Button --> */}
                    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-awesome fas fa-bars"></span>
                        <span class="navbar-toggler-awesome fas fa-times"></span>
                    </button>
                    {/* <!-- end of mobile menu toggle button --> */}

                    <div class="collapse navbar-collapse" id="navbarsExampleDefault">
                        <ul class="navbar-nav ml-auto">
                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#header">HOME <span class="sr-only">(current)</span></a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#intro">INTRO</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#services">SERVICES</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#callMe">CALL ME</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#projects">PROJECTS</a>
                            </li>

                            {/* <!-- Dropdown Menu --> */}
                            <li class="nav-item dropdown">
                                <a class="nav-link dropdown-toggle page-scroll" href="#about" id="navbarDropdown" role="button" aria-haspopup="true" aria-expanded="false">ABOUT</a>
                                <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                                    <a class="dropdown-item" href="terms-conditions.html"><span class="item-text">TERMS CONDITIONS</span></a>
                                    <div class="dropdown-items-divide-hr"></div>
                                    <Link to="/privacypolicy" class="dropdown-item" ><span class="item-text">PRIVACY POLICY</span></Link>
                                </div>
                            </li>
                            {/* <!-- end of dropdown menu --> */}

                            <li class="nav-item">
                                <a class="nav-link page-scroll" href="#contact">CONTACT</a>
                            </li>
                        </ul>
                        <span class="nav-item social-icons">
                            <span class="fa-stack">
                                <a href="#your-link">
                                    <span class="hexagon"></span>
                                    <i class="fab fa-facebook-f fa-stack-1x"></i>
                                </a>
                            </span>
                            <span class="fa-stack">
                                <a href="#your-link">
                                    <span class="hexagon"></span>
                                    <i class="fab fa-twitter fa-stack-1x"></i>
                                </a>
                            </span>
                        </span>
                    </div>
                </nav>
                {/* <!-- end of navbar --> */}
                {/* <!-- end of navbar --> */}
                {/* <!-- Header --> */}
                <header id="header" class="header">
                    <div class="header-content">
                        <div class="container">
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="text-container">
                                        <h1>BUSINESS <span id="js-rotating">TEMPLATE, SERVICES, SOLUTIONS</span></h1>
                                        <p class="p-heading p-large">Aria is a top consultancy company specializing in business growth using online marketing and conversion optimization tactics</p>
                                        <a class="btn-solid-lg page-scroll" href="#intro">DISCOVER</a>
                                    </div>
                                </div>
                                {/* <!-- end of col --> */}
                            </div>
                            {/* <!-- end of row --> */}
                        </div>
                        {/* <!-- end of container --> */}
                    </div>
                    {/* <!-- end of header-content --> */}
                </header>
                {/* <!-- end of header --> */}
                {/* <!-- end of header --> */}
            </>

        )
    }
}
export default Header;